.App {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    font-family: "Rubik", sans-serif;
    color: rgb(2, 178, 116);
    background: rgb(76, 246, 255);
    background: linear-gradient(
        135deg,
        rgba(76, 246, 255, 1) 0%,
        rgba(90, 252, 219, 1) 50%,
        rgba(250, 255, 86, 1) 50%
    );
}
