.JokeList {
    display: flex;
    width: 80%;
    height: 80%;
}
.JokeList-loading {
    text-align: center;
    color: white;
    margin: auto;
}
.JokeList-loading h1 {
    font-weight: 700;
    color: #9575cd;
}
.JokeList-loading i {
    color: #9575cd;
    margin-bottom: 40px;
}
.JokeList-sidebar {
    background-color: #9575cd;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    width: 30%;
    box-shadow: -1px -2px 33px 14px rgba(0, 0, 0, 0.24);
    z-index: 1;
}
.JokeList-title {
    font-size: 3rem;
    margin: 0rem;
    color: white;
    font-weight: 300;
    letter-spacing: 0.4rem;
}
.JokeList-title span {
    font-weight: 700;
    letter-spacing: normal;
}
.JokeList-sidebar img {
    box-sizing: border-box;
    padding: 5px;
    width: 50%;
    border-radius: 50%;
    box-shadow: -1px -2px 33px 14px rgba(0, 0, 0, 0.24);
    margin: 25px 0;
}
#btn-NewJokes {
    box-sizing: border-box;
    background: rgb(76, 246, 255);
    background: linear-gradient(
        135deg,
        rgba(76, 246, 255, 1) 0%,
        rgba(90, 252, 219, 1) 50%,
        rgba(250, 255, 86, 1) 50%
    );
    border-radius: 15px;
    border: 3px solid white;
    color: darkblue;
    transition: 0.8s cubic-bezier(0.19, 1, 0.22, 1);
    word-spacing: 2rem;
    margin: 20px;
    padding: 10px;
}
#btn-NewJokes:hover {
    background: rgb(250, 255, 86);
    background: linear-gradient(
        135deg,
        rgba(250, 255, 86, 1) 0%,
        rgba(250, 255, 86, 1) 50%,
        rgba(90, 252, 219, 1) 50%
    );
    transform: translateY(-3px);
}

.JokeList-jokes {
    width: 70%;
    height: 90%;
    align-self: center;
    background-color: aliceblue;
    overflow: scroll;
    box-shadow: -1px -2px 33px 14px rgba(0, 0, 0, 0.24);
}
