.Joke {
    display: flex;
    box-sizing: border-box;
    align-items: center;
    justify-content: center;
    border-bottom: #eeeeee solid 2px;
    padding: 1rem;
}
.Joke-button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 15%;
    margin-right: 1rem;
}
.Joke-text {
    width: 75%;
    font-size: 1.2rem;
}
.Joke-vote {
    width: 50px;
    height: 50px;
    line-height: 45px;
    border-radius: 50%;
    text-align: center;
    font-size: 20px;
    border: solid #ff0000 3px;
    font-weight: 300;
    margin: auto;
}
.fas {
    margin: 10px;
    font-size: 15px;
    cursor: pointer;
}
.Joke-smile {
    font-size: 1.5rem;
    margin-left: auto;
}
.Joke-smile :hover {
    animation-name: spin;
    animation-duration: 0.7s;
    animation-timing-function: ease-in-out;
    animation-delay: 0s;
}
@keyframes move-up {
    0%,
    50%,
    100% {
        transform: translateY(0);
    }
    25%,
    75% {
        transform: translateY(-3px);
    }
}
@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    30% {
        transform: rotate(20deg);
    }
    60% {
        transform: rotate(0deg);
    }
    80% {
        transform: rotate(10deg);
    }
    100% {
        transform: rotate(0deg);
    }
}
